<template>
  <div class="page-container">
    <div class="nav">
      <mt-navbar>
        <mt-tab-item
          v-for="route in routes"
          :key="route.path">
          <div class="navbar-item">
            <router-link :to="route.path">
              <span>{{ route.meta.title || route.name }}</span>
            </router-link>
          </div>
        </mt-tab-item>
      </mt-navbar>

      <div class="nav-filter">
        <mt-cell>
          <div slot="title" class="page-help">
            <router-link :to="helpLink">
              <span>{{ help }}</span>
            </router-link>
          </div>
          <span class="page-filter" v-on:click="openFilter()">
            筛选
            <i class="fa fa-filter" />
          </span>
        </mt-cell>
      </div>
    </div>

    <div class="content-container">
      <router-view></router-view>
    </div>

    <mt-popup v-model="filterVisible" position="right" class="filter-popup">
      <div class="filter-content">
        <slot name="filter" />
      </div>
    </mt-popup>
  </div>
</template>

<script>
export default {
  name: 'Layout',
  props: {
    routes: {
      type: Array,
      default: () => [],
    },
    helpLink: {
      type: String,
      default: '',
    },
    help: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      filterVisible: false,
    };
  },
  methods: {
    openFilter() {
      this.filterVisible = true;
    },
  },
};
</script>

<style lang='scss' scoped>
.nav {
  top: 0;
  right: 0;
  left: 0;
  position: fixed;
  z-index: 1;

  .navbar-item {
    padding: 0 5px;
    font-size: 14px;
  }

  .mint-navbar .mint-tab-item {
    background-color: #f44336;
    padding: 15px 0;

    a {
      color: white;
      padding-bottom: 2px;
    }

    a:active, a:hover, a.router-link-active {
      font-weight: bold;
      color: white;
      border-bottom: solid 2px white;
    }

    &.is-selected {
      border-bottom: none;
      margin-bottom: 0;
    }
  }

  .mint-tab-item + .mint-tab-item {
    border-left: solid 1px gainsboro;
  }

  .nav-filter {
    border-top: solid 1px gainsboro;
    background-color: #f5f5f5;

    .mint-cell {
      background-color: #f5f5f5;
      min-height: inherit;
      padding: 10px 0;
    }

    .page-help, .page-filter {
      font-size: 13px;
    }
  }
}

.content-container {
  padding-top: 80px;
}

.filter-popup {
  width: 50%;
  height: 100%;
  background-color: #fafafa;
  overflow: auto;

  .filter-content {
    padding: 20px 0;
  }
}
</style>

<style lang='scss'>
.filter-popup {
  .mint-cell-wrapper {
    font-size: 14px;
  }
}

.nav-filter {
  .mint-cell-wrapper {
    background-image: none;
  }
}
</style>



